.bannerbtn {position: relative;
      padding: 9px 30px 7px 30px;
      font-size: 16px;
      background: transparent;
      border: 2px solid #0382EA;
      border-radius: 8px;
      cursor: pointer;
      overflow: hidden;
      user-select: none;
      color:#0382EA;
      transition: all 0.3s ease;
      display: inline-block;
      font-weight: 400;
      margin-top:15px;
    }

    .bannerbtn::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      width: 0%;
      height: 0%;
      background-color: #0382EA;
      transition: all 0.4s ease;
      z-index: 0;
      border-top-left-radius: 100%;
    }

    .bannerbtn:hover::before {
      width: 250%;
      height: 250%;
    }

    .btn-content {
      position: relative;
      display: inline-block;
      overflow: hidden;
      z-index: 1;
    }

    .btn-content span {
      display: block;
      transition: transform 0.4s ease;
      text-align: center;
    }

    .btn-content .text-top {
      color: #0382EA;
      transform: translateY(0%);
    }

    .btn-content .text-bottom {
      color: white;
      transform: translateY(100%);
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
    }

    .bannerbtn:hover .text-top {
      transform: translateY(-100%);
    }

    .bannerbtn:hover .text-bottom {
      transform: translateY(0%);
    }

.bannerbtn::after {
    content: "";
    position: absolute;
    background: #0382EA;
    height: 35px;
    width: 17px;
    right: -7px;
    bottom: -18px;
    /* border-radius: 116% 9% 0 0; */
    rotate: 216deg;
}
.bannerbtn:hover {
box-shadow: 0 0 12px #79baef, 0 0 24px #79baef;
}

