.brand-slider-wrapper {
  overflow: hidden;
  width: 100%;
  padding: 20px 0;
}

.brand-slider {
  display: flex;
  width: max-content;
  animation: scrollBrands 15s linear infinite;
}

.brand-slider:hover {
  animation-play-state: paused;
}

.brand-slide {
  flex: 0 0 auto;
  width:200px;
}

.brand-slide img {
  width: 100%;
  height: auto;
  display: block;
}

/* Seamless horizontal scroll */
@keyframes scrollBrands {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}






